body {
  margin: 0;
  padding: 0;
  font-family: 'Arial';
  background: url('https://i.kinja-img.com/gawker-media/image/upload/t_original/jicoosz1kuq8pepov61s.jpg');
  background-size: cover;
  background-repeat: no-repeat;
}

.astros {
	background: white;
	border-radius: 10px;
	width: 60%;
	height: auto;
	margin-left: auto;
	margin-right:auto;
}

#count {
	text-align: center;
	margin: auto 100px;
}

#count-button {
		display: -ms-flexbox;
		display: flex;
		-ms-flex-pack: center;
		    justify-content: center;
		-webkit-transition-duration: 0.4s;
		     -o-transition-duration: 0.4s;
		        transition-duration: 0.4s;
		background-color: #4CAF50; /* Green */
    border: none;
    border-radius: 10px;
    color: white;
    padding: 15px 32px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    margin: auto 20px auto 20px;
}

#count-button:hover {
	background-color: blue;
	color: orange;
}


